body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}
.content-span{
  display: inline-block;
  width: 80px;;
}
.moveClick{
  color:#ff6600;
  font-weight: 600;
}

.game-board-button{
  margin-top: 5px;
  margin-right: 5px;
}
.game-board{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 30%;
}
.game-info {
  margin-left: 20px;
  margin-right: 20px;
  width: 30%;
}
.game-step{
  width: 30%;
}
.title{
  text-align: center;
}
.hidding{
  display: none;
}
